.service_container{
    
   .service_banner{
    width: 100%;
    height: 92vh;
    background: url(../images/service_banner.webp);
    background-position: center;
    background-size: cover;
    position: relative;

    .overlay{
        width: inherit;
        height: inherit;
        background-image: linear-gradient(to top,black,transparent);
 
        position: absolute;

        &:nth-child(2){
            width: inherit;
            height: inherit;
            background-image: linear-gradient(to top,rgba(0, 0, 0, 0.774),transparent,transparent);
            transform: scaleY(-1);
            opacity: .5;
        }
    }

    .service_banner_text{
        width: 90%;
        margin: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        top: 80%;
        left: 5%;
        
        
        
        .first_text{
            width: 20%;
        
            h1{
                color: rgb(240, 95, 12);
                font-size: clamp(1rem,3vw,3rem);
                margin-bottom: 0;


            }
            width: 80%;
            p{
                color: white;
                margin-top: 0;
                text-align: left;
            }
        }

        .second_text{
            margin-top: 20px;
            p{
                color: white;
                text-align: left;
                line-height: 1.7;
            }
        }
        
    }

   }

   .text_container{
    display: flex;
    width: 90%;
    margin: auto;
    justify-content: space-between;
    align-items: center;
    margin-top: 120px;
    


    .div1{
        width: 25%;
        
        h1{
            font-size: clamp(1rem,3vw,3rem);
            text-align: left;
            color: #D9592F;
            margin: 0;
        }
    }

    

    .div2{
        width: 65%;

        p{
            border-left: 1px solid rgb(208, 208, 208);
            text-align: left;
            padding-left: 50px;
            font-size: .95;
            color: rgb(84, 84, 84);
            line-height: 2;
        }
    }
   }

   


